import Login from '@/layout/login';
import Entry from '@/layout/entry';
import Nomatch from '@/layout/nomatch';
import Org from '@/layout/market/org';
import Teacher from '@/layout/market/teacher';
let groups = {
  marketManger: {
    groupName: '市场管理'
  },
  studyManger: {
    groupName: '教学管理'
  }
};

let routes = [
  {
    path: '/',
    name: 'entry',
    component: Entry,
    children: [
      {
        name: 'org',
        path: 'org',
        component: Org,
        meta: {
          title: '架构组织',
          groupName: 'marketManger',
          isAuth: true
        }
      },
      {
        name: 'teacher',
        path: 'teacher',
        component: Teacher,
        meta: {
          title: '市场老师',
          groupName: 'marketManger'
        }
      },
      {
        name: 'student',
        path: 'student',
        component: () => import('@/layout/market/student'),
        meta: {
          title: '招生管理',
          groupName: 'marketManger'
        },
      },
      {
        path: 'test',
        name: 'test',
        componnet: () => import('@/layout/test'),
        meta: {
          title: '测试',
          groupName: 'studyManger'
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '*',
    name: 'nomatch',
    component: Nomatch
  }
];

// 封装一个可以提取出侧边栏数据的方法
function getMenus(){
  let rootPath = routes[0].path;
  let original = routes[0].children;
  let menus = Object.keys(groups).map(item => {
    let subMenus = original.filter(val => val.meta.groupName === item).map(val => {
      return {
        path: rootPath + val.path,
        title: val.meta.title
      };
    });
    return {
      groupName: groups[item].groupName,
      sign: item,
      subMenus
    };
  });
  return menus;
}
export { routes, getMenus};