<template>
  <el-container>
    <el-header>市场人员信息录入系统</el-header>
    <el-main
      v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <div class="form-box">
        <el-form :model="validateForm" ref="validateForm" class="demo-ruleForm">
          <el-form-item
            prop="username"
            :rules="[
              { required: true, message: '不能为空'}
            ]"
          >
            <el-input
              type="username"
              v-model.number="validateForm.username"
              autocomplete="off"
              placeholder="账户名"
            ></el-input>
          </el-form-item>
          <el-form-item
            prop="password"
