            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('validateForm')">登陆</el-button>
            <el-button @click="resetForm('validateForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-main>
    <el-footer>Copyright © 2019 - 2020 XX团队. All Rights Reserved.</el-footer>
  </el-container>
</template>

