        password: ""
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          
          this.loading = true;
          setTimeout(()=>{
            this.$router.push("/");
          },500)
          
          // this.$http
          //   .post("/student/login", {
          //     student_id: this.validateForm.stuNum.toString(),
          //     student_pwd: this.validateForm.password
          //   })
          //   .then(
          //     res => {
          //       this.loading = false;
          //       if (res.code == 1) {
          //         setCookie('student_token',res.token,3)
                  
          //         this.$router.push("/");
          //       } else {
          //         this.$alert("用户名或密码有误！", "提示", {
          //           confirmButtonText: "重新输入"
          //         })
          //       }
          //     },
          //     err => {
          //       this.loading = false;
          //       this.$message({
          //         showClose: true,
          //         message: `${err}，请重试`,
          //         type: "error"
          //       });
          //     }
          //   );
        } else {
          this.$message({
            showClose: true,
            message: "请输入账号或密码",
