};
</script>

<style scoped>
.el-container {
  height: 100%;
}
.el-header,
.el-footer {
  color: #333;
  line-height: 60px;
}
.el-header {
  font-size: 20px;
  box-shadow: 0 0 5px #eee;
}
.el-footer {
  font-size: 12px;
  text-align: center;
}
.el-main {
  color: #333;
  text-align: center;
  position: relative;
  background: #f4f4f4;
}

.form-box {
  width: 37.5%;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate3d(-50%, -50%, 0);
  background: #fff;
  box-sizing: border-box;
  padding-top: 10px;
  box-shadow: 0 0 8px #eee;
}
