<template>
    <div class="block" v-loading="treeLoading">
        <p>组织架构管理</p>
        <el-tree
            :data="data"
            :props="{label:'orgName',children:'children'}"
            :load="loadNode"
            node-key="id"
            :expand-on-click-node="false"
            lazy
        >
            <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>{{ node.label }}</span>
                <span>
                    <el-button type="text" size="mini" @click="() => append(data)">添加</el-button>
                    <el-button type="text" size="mini" @click="() => remove(node, data)">删除</el-button>
                </span>
            </span>
        </el-tree>
        <el-dialog title="添加组织架构" :visible.sync="dialogFormVisible">
            <el-form
                :model="ruleForm"
                status-icon
                :rules="rules"
                ref="ruleForm"
                label-width="100px"
                class="demo-ruleForm"
            >
                <el-form-item label="组织名称" prop="orgName">
                    <el-input v-model="ruleForm.orgName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="组织编码" prop="orgCode">
                    <el-input v-model.number="ruleForm.orgCode"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button @click="resetForm('ruleForm')">重 置</el-button>
                <el-button type="primary" @click="addOrg" :loading="isAddOrg">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    selectedOrg: null,
    data() {
        var checkOrgCode = (rule, value, callback) => {
            if (!Number.isInteger(value)) {
                callback(new Error("请输入编码，格式为数字。"));
            } else {
                if (value < 10000) {
                    callback(new Error("数字必须大于10000"));
                } else {
                    callback();
                }
            }
        };
        var validateOrgName = (rule, value, callback) => {
            if (value === "") {
                callback(new Error("请输入组织名称"));
            } else {
                callback();
            }
        };
        return {
            treeLoading: false,
            data: [],
            dialogFormVisible: false,
            ruleForm: {
                orgName: "",
                orgCode: ""
            },
            rules: {
                orgName: [{ validator: validateOrgName, trigger: "blur" }],
                orgCode: [{ validator: checkOrgCode, trigger: "blur" }]
            },
            isAddOrg: false
        };
    },
    async mounted() {
        //初始化tree数据
        let data = await this.getDepartment();
        this.data = data;
    },
    methods: {
        async getDepartment(parentCode) {
            this.treeLoading = true;
            let query = parentCode ? `parentCode=${parentCode}` : ``;
            let res = await this.$http.get(`/org_chart/getDepartment?${query}`);
            this.treeLoading = false;
            if (res.code == 1) {
                return res.data;
            } else {
                this.$message({
                    message: res.msg,
                    type: "warning"
                });
                return [];
            }
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        loadNode(node, resolve) {
            if (node.level > 0) {
                return this.getDepartment(node.data.orgCode).then(data => {
                    resolve(data);
                });
            }
        },
        addOrg() {
            this.$refs["ruleForm"].validate(async valid => {
                if (valid) {
                    this.isAddOrg = true;
                    //添加组织架构
                    let {
                        result: addRes,
                        error: addError
                    } = await this.$http2.post(`/org_chart/addDepartment`, {
                        orgCode: this.ruleForm.orgCode,
                        orgName: this.ruleForm.orgName,
                        parentCode: this.selectedOrg.orgCode
                    });

                    this.isAddOrg = false;

                    this.$message({
                        message: addError || addRes.msg,
                        type: "info"
                    });
                    if (addError) return;

                    //重新初始化tree数据
                    let data = await this.getDepartment();
                    this.data = data;
                    this.dialogFormVisible = false;
                } else {
                    return false;
                }
            });
        },
        async append(data) {
            this.dialogFormVisible = true;
            this.selectedOrg = data;
        },
        async remove(node, data) {
            let { result, error } = await this.$http2.post(
                `/org_chart/delDepartment`,
                { orgCode: data.orgCode, deep: false }
            );

            if (result.code == 1) {
                let data = await this.getDepartment();
                this.data = data;
                this.$message({ message: result.msg });
            }
        }
    }
};
</script>

<style>
.custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
}
</style>