import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
import {routes} from './routes';
let config = {
  mode: 'hash',
  routes
};
// console.log(getMenus());
let router = new VueRouter(config);

router.beforeEach((to,from,next)=>{
  //暂时限制不能进入架构组织操作，如果需要进入org页面在路径后加?key=001
  if(typeof to.meta.isAuth === 'boolean' && to.meta.isAuth && to.query.key!=='001'){
    next({
      name:from.name === to.name? 'entry':from.name
    });
  }else{
    next();
  }

});


export default router;