<template>
    <el-container style="height: 100%;">
        <el-aside width="100px" style="background-color: #001135; line-height: 60px; color:#fff;">
            <el-menu @select="menuSelect" :default-active="menusData[activeMenuIndex].sign">
                <el-menu-item v-for="item in menusData" :key="item.sign" :index="item.sign">{{item.groupName}}</el-menu-item>
            </el-menu>
        </el-aside>

        <el-container>
            <el-aside width="140px" style="background-color: #061D48; color: #fff;">
                <el-menu @select="subMenuSelect" :default-active="willToPath">
                    <el-menu-item 
                        v-for="(item) in menusData[activeMenuIndex].subMenus"
                        :key="item.path"
                        :index="item.path"
                    >
                        {{item.title}}
                    </el-menu-item>
                </el-menu>
            </el-aside>
            <el-container>
                <el-header style="text-align: right; line-height:60px; font-size: 16px">
                    <el-row>
                        <el-col :span="12">
                            <h1>{{$route.meta.title || '信息面板'}}</h1>
                        </el-col>
                        <el-col :span="12">
                            <el-dropdown>
                                <i class="el-icon-setting" style="margin-right: 15px"></i>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item>查看</el-dropdown-item>
                                    <el-dropdown-item>新增</el-dropdown-item>
                                    <el-dropdown-item>删除</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                            <span>王小虎</span>
                        </el-col>
                    </el-row>
                </el-header>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </el-container>
</template>

<script>
import {getMenus} from '../router/routes'
export default {
    data() {
        window.aa = this;
        return {
            menusData: getMenus(),
            activeMenuIndex: 0,
            activeSubMenuIndex: 0
        };
    },
    computed: {
        willToPath(){
            return this.menusData[this.activeMenuIndex].subMenus[this.activeSubMenuIndex].path
        }
    },
    methods: {
        menuSelect(index){
            this.activeMenuIndex = this.menusData.findIndex(item => item.sign === index);
            this.activeSubMenuIndex = 0;    
            this.$router.push(this.willToPath);
        },
        subMenuSelect(index){
            this.activeSubMenuIndex = this.menusData[this.activeMenuIndex].subMenus.findIndex(item => item.path === index);
            this.$router.push(this.willToPath);
        },
        getPosFromUrl(){
            let path = this.$route.path;
            let menuIndex = 0;
            let subMenuIndex = 0;
            for(let i = 0; i < this.menusData.length; i++){
                let index = this.menusData[i].subMenus.findIndex(item => path.indexOf(item.path) !== -1);
                if(index !== -1){
                    menuIndex = i;
                    subMenuIndex = index;
                }
            }
            this.activeMenuIndex = menuIndex;
            this.activeSubMenuIndex = subMenuIndex;
        }
    },
    mounted(){
        this.getPosFromUrl();
    },
    watch:{
        '$route'(){
            this.getPosFromUrl();
        }
    }
};
</script>

<style scoped>
.el-footer{
    border-top: 1px solid #eee;
}
.el-menu {
    background: none;
    border-right: none;
}
.el-menu-item {
    color: #7d8399;
}
.el-menu-item.is-active {
    border-left: none;
    color: #fff;
}
.el-menu-item:focus, .el-menu-item:hover{
    background: #061D48;
}
</style>
