import axios from 'axios';
import { getCookie } from './utils';
import { Message } from 'element-ui';
let httpInstance = axios.create({
  timeout:8000,
  headers:{
    'content-type':'application/json'
  }
});
let httpInstance2 = axios.create({
  timeout:8000,
  headers:{
    'content-type':'application/json'
  }
});
httpInstance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  if(getCookie('student_token')){
    // config.headers['Authorization']= getCookie('student_token')
  }

  return config;
}, function (error) {
  Message({
    message:error,
    type:'error'
  });
  // 对请求错误做些什么
  return Promise.reject(error);
});

httpInstance.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  return response.data;
    
}, function (error) {
  Message({
    message:error,
    type:'error'
  });
  // 对响应错误做点什么
  return Promise.reject(error);
});

httpInstance2.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  if(getCookie('student_token')){
    // config.headers['Authorization']= getCookie('student_token')
  }
  return config;
}, function (error) {
  Message({
    message:error,
    type:'error'
  });
  // 对请求错误做些什么
  return Promise.reject(error);
});

httpInstance2.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  return {result:response.data};
    
}, function (error) {
  console.log(error);
  // 对响应错误做点什么
  return Promise.resolve({error});
});

export { httpInstance as axios };

export default {
  install(V) {
    Object.defineProperty( V.prototype, '$http',{
      value: httpInstance
    });
    Object.defineProperty( V.prototype, '$http2',{
      value: httpInstance2
    });
  }
};