<template>
    <el-main>
        <el-row>
            <el-form
                :inline="true"
                :model="marketTeacher"
                label-position="right"
                class="demo-form-inline"
                label-width="75px"
                size="small"
            >
                <el-form-item label="姓名:">
                    <el-input v-model="marketTeacher.name" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="单位:">
                    <el-select v-model="marketTeacher.orgCode" placeholder="请选择">
                        <el-option
                        v-for="item in orgList"
                        :key="item.orgCode"
                        :label="item.orgName"
                        :value="item.orgCode">
                        </el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-form-item label="推荐人:">
                    <el-select v-model="marketTeacher.referrer" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="身份证号:">
                    <el-input v-model="marketTeacher.idCard" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="家庭住址:">
                    <el-input v-model="marketTeacher.homeAdd" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="登记日期:">
                    <el-date-picker
                        v-model="marketTeacher.recordDate"
                        type="datetime"
                        placeholder="选择日期时间"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="checkMarket" class="market-teacher-check">查询</el-button>
                </el-form-item>
            </el-form>
        </el-row>
        <el-row class="market-teacher-table-operation">
            <el-button type="primary" @click="addTeacher">添加</el-button>
            <el-button type="primary" @click="inputTeacher">导入</el-button>
            <el-button type="primary" @click="outputTeacher">导出</el-button>
        </el-row>
        <el-table
            ref="multipleTable"
            :data="marketT"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
        >
            <el-table-column align="center" type="selection"></el-table-column>
            <el-table-column align="center" prop="name" label="姓名"></el-table-column>
            <el-table-column align="center" prop="sex" label="性别">
                <template slot-scope="scope">{{scope.row.sex === 1 ? '男' : '女'}}</template>
            </el-table-column>
            <el-table-column align="center" label="单位" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.orgCode}}</template>
            </el-table-column>
            <el-table-column align="center" prop="idCard" label="身份证号" width="120px"></el-table-column>
            <el-table-column align="center" label="毕业院校" show-overflow-tooltip>
                <template
                    slot-scope="scope"
                >{{scope.row.graduateSchool ? scope.row.graduateSchool : '-'}}</template>
            </el-table-column>
            <el-table-column align="center" prop="homeAdd" label="家庭住址"></el-table-column>
            <el-table-column
                align="center"
                prop="recordDate"
                label="登记日期"
                width="200px"
                show-overflow-tooltip
            >
                <template slot-scope="scope">
                    {{parseTime(scope.row.recordDate)}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="120">
                <template slot-scope="scope">
                    <el-button type="text" size="small">编辑</el-button>
                    <el-button type="text" size="small" @click="delTeacher(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="marketTeacher.pageIndex"
            :page-sizes="[5, 10, 20, 40]"
            :page-size="marketTeacher.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="marketT.length"
        ></el-pagination>
        <el-dialog title="添加老师" :visible.sync="dialogFormVisible">
            <el-form ref="addNewTeacher" :model="newTeacher" inline label-width="80px">
                <el-form-item
                    :rules="[{ required: true, message: '名字不能为空'}]"
                    label="名字"
                    prop="name"
                >
                    <el-input v-model="newTeacher.name"></el-input>
                </el-form-item>
                <el-form-item :rules="[{ required: true, message: '性别必须选择'}]" label="性别" prop="sex">
                    <el-radio v-model="newTeacher.sex" :label="1">男</el-radio>
                    <el-radio v-model="newTeacher.sex" :label="0">女</el-radio>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: '身份证号必填'}]"
                    label="身份证号"
                    prop="idCard"
                >
                    <el-input v-model="newTeacher.idCard"></el-input>
                </el-form-item>
                <el-form-item label="毕业院校">
                    <el-input v-model="newTeacher.graduateSchool"></el-input>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: '家庭住址必填'}]"
                    label="家庭住址"
                    prop="homeAdd"
                >
                    <el-input v-model="newTeacher.homeAdd"></el-input>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: '登记日期必选'}]"
                    label="登记日期"
                    prop="recordDate"
                >
                    <el-date-picker
                        v-model="newTeacher.recordDate"
                        type="datetime"
                        placeholder="选择日期时间"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="推荐人">
                    <el-select v-model="newTeacher.referrer" placeholder="请选择">
                        <el-option
                        v-for="item in marketT"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item
                    :rules="[{ required: true, message: '所属单位必选'}]"
                    label="所属单位"
                    prop="orgCode"
                >
                    <el-select v-model="newTeacher.orgCode" placeholder="请选择">
                        <el-option
                        v-for="item in orgList"
                        :key="item.orgCode"
                        :label="item.orgName"
                        :value="item.orgCode">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="closeDailog('addNewTeacher')">取 消</el-button>
                <el-button type="primary" @click="sureAdd('addNewTeacher')">确 定</el-button>
            </div>
        </el-dialog>
    </el-main>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { parseTime, filterObjectemptyKey } from "@/utils";
import { MessageBox } from 'element-ui';
export default {
    data() {
        return {
            currentPage: 1,
            marketTeacher: {
                name: "",
                orgCode: "",
                referrer: "",
                idCard: "",
                homeAdd: "",
                recordDate: "",
                pageIndex: 1,
                pageSize: 5
            },
            dialogFormVisible: false,
            newTeacher: {
                name: "",
                sex: "",
                idCard: "",
                graduateSchool: "",
                homeAdd: "",
                recordDate: "",
                referrer: "",
                orgCode: ""
            }
        };
    },
    computed: {
        ...mapState({
            marketT: state => {
                return state.marketTeacher.marketTeacher;
            },
            orgList: state => {
                return state.marketTeacher.orgList;
            }
        })
    },
    mounted() {
        let newObj = filterObjectemptyKey(this.marketTeacher);
        this.getTeacher(newObj);
        this.getOrglist()
    },
    methods: {
        ...mapActions({
            getTeacher: "marketTeacher/getTeacher",
            getOrglist: "marketTeacher/getOrglist"
        }),
        parseTime,
        checkMarket() {
            let newObj = filterObjectemptyKey(this.marketTeacher);
            this.getTeacher({ ...newObj, pageIndex: 1, pageSize: 5 });
        },
        // 展示弹框
        addTeacher() {
            this.dialogFormVisible = true;
        },
        closeDailog(formName) {
            this.dialogFormVisible = false;
            this.$refs[formName].resetFields();
        },
        // 添加市场老师
        sureAdd(addTeacher) {
            this.$refs[addTeacher].validate(valid => {
                if (valid) {
                    let newObj = filterObjectemptyKey(this.newTeacher);

                    this.$http
                        .post("/market_teacher/addTeacher", {...newObj, recordDate: parseTime(newObj.recordDate)})
                        .then(res => {
                            this.closeDailog(addTeacher);
                            let newObj = filterObjectemptyKey(this.marketTeacher);
                            this.getTeacher({ ...newObj, pageIndex: 1, pageSize: 5 });
                        });
                } else {
                    return false;
                }
            });
        },
        // 导入市场老师信息
        inputTeacher() {},
        // 导出所选中市场老师信息
        outputTeacher() {},
        // table表格多选触发
        handleSelectionChange() {},
        // 每页条数变化的回调
        handleSizeChange(size) {
            this.marketTeacher.pageSize = size;
            this.getTeacher({ ...this.marketTeacher});
        },
        // 修改当前页码回调
        handleCurrentChange(page) {
            this.marketTeacher.pageIndex = page;
            this.getTeacher({ ...this.marketTeacher});
        },
        // 删除教师
        delTeacher(id) {
            MessageBox.confirm('此操作将会造成该人员的流失, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'error',
                center: true
            }).then(res => {
                this.$http
                .post("/market_teacher/delTeacher", {
                    id
                })
                .then(res => {
                    this.getTeacher();
                });
            })
            
        }
    }
};
</script>

<style scoped>
.market-teacher-check {
    float: right;
}
.market-teacher-table-operation {
    float: right;
    padding-top: 15px;
}
</style>

<style>
</style>








